
import { createItem } from '@/api/doctors/doctors'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

const genderOptions = [
{ key: 'male', value: '男' },
{ key: 'female', value: '女' }
]
export default {
name: 'CreateChild',
components: {
Group,
XInput,
PopupPicker,
PopupRadio,
Datetime
},
data() {
return {
genderOptions,
labelWidth: '15',
temp: {
name: undefined,
principal: undefined,
phone: undefined,
email: undefined,
password: undefined,
password_confirmation: undefined,
address: undefined,
job_title: undefined,
description: undefined
}
}
},
methods: {
handleClickSave() {

if (this.temp.name === undefined || this.temp.name === '') {
this.$vux.toast.text('请输入名称', 'center')
return
}
if (this.temp.email === undefined || this.temp.email === '') {
this.$vux.toast.text('请输入邮箱', 'center')
return
}
if (this.temp.password === undefined || this.temp.password === '') {
this.$vux.toast.text('请输入密码', 'center')
return
}
if (this.temp.password !== this.temp.password_confirmation) {
this.$vux.toast.text('两次输入密码不一致，请重新填写', 'center')
return
}
if (this.temp.password.length < 6) {
this.$vux.toast.text('密码长度不足6位', 'center')
return
}
createItem(this.temp).then(res => {
console.log('注册成功')
}).catch(error => {
console.log('error => ', error)
this.$vux.toast.text(error, 'center')
})
},
handleClear(val) {
console.log('val => ', val)
}
}
}



